










































































import { Vue, Component } from 'vue-property-decorator';
import WebXProgress, { EProgressMode } from '@advantech/web-x-component-progress';

@Component({
    components: { WebXProgress },
})
export default class VuePageClass extends Vue {
    private eProgressMode = EProgressMode;
    private showProgressModeCount: boolean = false;
    private showProgressModePercentage: boolean = false;

    private mode: EProgressMode = EProgressMode.count;
    private max: number = 25;
    private value: number = 0;

    private async delay(timeMs: number = 500): Promise<void> {
        await new Promise<void>((resolve) => {
            setTimeout(() => resolve(), timeMs);
        });
    }

    private async clickShowCount(): Promise<void> {
        this.showProgressModeCount = true;
        this.mode = EProgressMode.count;
        this.value = 1;
        for (let index = 0; index <= this.max; index++) {
            this.value = index;
            await this.delay(100);
        }

        await this.delay(200);
        if (this.max === this.value) {
            this.showProgressModeCount = false;
        }
    }

    private async clickShowPercentage(): Promise<void> {
        this.showProgressModePercentage = true;
        this.mode = EProgressMode.percentage;
        this.value = 1;
        for (let index = 0; index <= this.max; index++) {
            this.value = index;
            await this.delay(100);
        }

        await this.delay(200);
        if (this.max === this.value) {
            this.showProgressModePercentage = false;
        }
    }

    private inputMax(): void {
        // let _max: number = this.max;
        this.max = Number(this.max);
    }
}
